<template>
  <div class="page-card-content" v-loading="loading">
    <div class="top-area">
      <el-descriptions>
        <el-descriptions-item label="平台名称">{{ row.name }}</el-descriptions-item>
        <el-descriptions-item label="平台描述">{{ row.desc }}</el-descriptions-item>
        <el-descriptions-item label="链接">{{ row.url }}</el-descriptions-item>
      </el-descriptions>
      <el-button type="primary" @click="handleRowClick('add')">新增</el-button>
    </div>
    <el-table
      :data="tableData"
      stripe
      row-key="id"
      :header-cell-style="{ background: '#f7f8fb' }"
      height="620"
      default-expand-all
      :tree-props="{ children: 'children', hasChildren: 'haChildren' }"
    >
      >
      <template slot="empty">
        <div class="empty-img">
          <img src="@/assets/img/empty.png" />
          <p>暂无数据</p>
        </div>
      </template>
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="名称" prop="name"> </el-table-column>
      <el-table-column label="图标" prop="icon">
        <template slot-scope="scope">
          <img v-if="scope.row.icon" :src="scope.row.icon" style="width:20px;height:20px" />
          <span v-else>无</span>
        </template>
      </el-table-column>
      <el-table-column label="授权标识" prop="code"></el-table-column>
      <el-table-column label="类型" prop="type">
        <template slot-scope="scope">
          <span>{{ scope.row.type | filterType }}</span>
        </template>
      </el-table-column>
      <el-table-column label="排序" prop="sort"> </el-table-column>
      <el-table-column label="路由" prop="route"> </el-table-column>
      <el-table-column label="状态" prop="hided">
        <template slot-scope="scope">
          <span>{{ scope.row.hided | filterHided }}</span>
        </template></el-table-column
      >
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="handleRowClick('editor', scope.row)">编辑</el-button>
          <el-button type="text" @click="handleRowClick('detail', scope.row)">详情</el-button>
          <el-button type="text" @click="handleRowClick('hidden', scope.row)">{{
            scope.row.hided ? '隐藏' : '显示'
          }}</el-button>
          <el-button type="text" @click="handleRowClick('delete', scope.row)" style="color:#f56c6c"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <!-- 新增-->
    <el-dialog
      :title="operationType == 'editor' ? '编辑' : operationType == 'add' ? '新增' : '详情'"
      width="600px"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      label-width="120px"
      style="margin-top:-20px"
    >
      <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules">
        <el-form-item label="类型">
          <el-radio-group
            v-model="addForm.type"
            @change="choseType"
            :disabled="operationType == 'detail'"
          >
            <el-radio label="1">菜单</el-radio>
            <el-radio label="2">按钮</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input
            v-model="addForm.name"
            class="labelWidth"
            maxlength="6"
            :disabled="operationType == 'detail'"
            @input="changeMessage()"
          ></el-input>
        </el-form-item>
        <el-form-item label="排序" prop="sort">
          <el-input
            v-model="addForm.sort"
            class="labelWidth"
            maxlength="150"
            type="number"
            :disabled="operationType == 'detail'"
          ></el-input>
        </el-form-item>
        <el-form-item label="路由" prop="route" v-if="addForm.type == 1">
          <el-input
            v-model="addForm.route"
            class="labelWidth"
            maxlength="150"
            :disabled="operationType == 'detail'"
          ></el-input>
        </el-form-item>
        <el-form-item label="授权标识" prop="code">
          <el-input
            v-model="addForm.code"
            class="labelWidth"
            maxlength="150"
            :disabled="operationType != 'add'"
          ></el-input>
        </el-form-item>
        <el-form-item label="图标" v-if="addForm.type == 1">
          <el-upload
            ref="imgUpload"
            :action="''"
            :file-list="fileList"
            list-type="picture-card"
            :on-change="handleFileChange"
            :on-remove="handleRemove"
            :on-exceed="handleExceed"
            accept=".png,.jpeg,.jpg,.bmp"
            multiple
            :limit="1"
            :auto-upload="false"
          >
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item :label="addForm.type == 1 ? '上级菜单' : '菜单'" prop="parentId">
          <el-cascader
            :options="options"
            clearable
            v-model="addForm.parentId"
            :props="{
              checkStrictly: true,
              value: 'id',
              label: 'name',
              children: 'children'
            }"
            :disabled="operationType == 'detail'"
          ></el-cascader>
        </el-form-item>

        <el-form-item label="最后更新人" v-if="operationType == 'detail'">
          <el-input
            v-model="addForm.updater"
            class="labelWidth"
            :disabled="operationType == 'detail'"
          ></el-input>
        </el-form-item>
        <el-form-item label="最后更新时间" v-if="operationType == 'detail'">
          <el-input
            v-model="addForm.updateTime"
            class="labelWidth"
            :disabled="operationType == 'detail'"
          ></el-input>
        </el-form-item>
        <el-form-item label="创建人" v-if="operationType == 'detail'">
          <el-input
            v-model="addForm.creator"
            class="labelWidth"
            :disabled="operationType == 'detail'"
          ></el-input>
        </el-form-item>
        <el-form-item label="创建时间" v-if="operationType == 'detail'">
          <el-input
            v-model="addForm.createTime"
            class="labelWidth"
            :disabled="operationType == 'detail'"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" style="float: right;">
            {{ operationType == 'detail' ? '关闭' : '保存' }}
          </el-button>
          <el-button
            type="primary"
            @click="submitForm(1)"
            plain
            style="float: right;margin-right:15px"
            v-if="operationType != 'detail'"
          >
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import API from '@/api/index.js'

export default {
  filters: {
    filterType(val) {
      return val == 1 ? '菜单' : '按钮'
    },
    filterHided(val) {
      return val ? '显示' : '隐藏'
    }
  },
  data() {
    return {
      loading: false,
      tableData: [],
      pageInfo: {
        pageSizes: [10, 20, 50],
        total: 0
      },
      dialogVisible: false,
      operationType: 'add',
      addForm: {
        code: '',
        icon: '',
        name: '',
        parentId: 0,
        platformId: 0,
        route: '',
        sort: null,
        type: '1'
      },
      formRules: {
        name: [
          {
            trigger: 'blur',
            message: '请输入正确名称（支持中文，英文大小写）',
            required: true,
            pattern: '^[a-zA-Z\\u4e00-\\u9fa5]+$'
          }
        ],
        url: [
          {
            trigger: 'blur',
            message: '请输入正确名称（支持中文，英文大小写）',
            required: true
          }
        ],
        parentId: [
          {
            trigger: 'change',
            message: '请选择上级菜单',
            required: true
          }
        ]
      },
      options: [
        {
          hasChildren: true,
          id: -1,
          level: 0,
          name: '根目录',
          parentId: null,
          roleAuth: null,
          children: []
        }
      ],
      optionProps: {
        value: 'id',
        label: 'name',
        children: 'children'
      },
      fileList: [],
      row: {}
    }
  },
  created() {
    this.row = {
      id: this.$route.query.id,
      desc: this.$route.query.desc,
      name: this.$route.query.name,
      url: this.$route.query.url
    }
  },
  mounted() {
    this.getTableData()
    this.getMenuTree()
  },
  methods: {
    // 强制更新测试文本信息框的值
    changeMessage() {
      this.$forceUpdate()
    },
    choseType(e) {
      if (e == 2) {
        if (this.addForm.route) {
          delete this.addForm.route
        }
        if (this.addForm.icon) {
          delete this.addForm.icon
        }
      }
    },
    // 菜单树
    getMenuTree() {
      API.getMenuList({ platformId: this.row.id, type: 1 }).then(res => {
        if (res.status == 200) {
          this.options[0].children = res.data
        } else {
          this.$message.error(res.message)
        }
      })
    },
    submitForm(type) {
      if (type == 1) {
        this.$refs.addForm.resetFields()
        this.dialogVisible = false
        this.fileList = []
      } else {
        this.$refs.addForm.validate(valid => {
          if (valid) {
            this.addForm.parentId = this.addForm.parentId[this.addForm.parentId.length - 1]
            this.addForm.platformId = parseInt(this.row.id)
            this.addForm.sort = parseInt(this.addForm.sort)
            if (this.addForm.id) {
              API.getMenuModify(this.addForm).then(res => {
                if (res.status == 200) {
                  this.dialogVisible = false
                  this.$refs.addForm.resetFields()
                  this.$message.success('保存成功')
                  this.getTableData()
                  this.getMenuTree()
                  this.fileList = []
                } else {
                  this.$message.error(res.message)
                }
              })
            } else {
              API.getMenuAdd(this.addForm).then(res => {
                if (res.status == 200) {
                  this.dialogVisible = false
                  this.$refs.addForm.resetFields()
                  this.$message.success('保存成功')
                  this.getTableData()
                  this.getMenuTree()
                  this.fileList = []
                } else {
                  this.$message.error(res.message)
                }
              })
            }
          } else {
            this.$message.error('请检查表单内容是否填写完整')
          }
        })
      }
    },
    //图片数量限制
    handleImgExceed() {
      this.$message.warning('您当前选择的图标过多，最多只能上传一张图标')
    },
    handleFileChange(file, fileList) {
      this.fileList = fileList
      const formData = new FormData()
      formData.append('label', 'nice')
      formData.append('file', file.raw)
      API.uploadFile(formData).then(res => {
        if (res.status == 200) {
          this.addForm.icon = res.data
        }
      })
    },
    handleRemove(file, fileList) {
      this.fileList = fileList
      this.addForm.icon = ''
    },
    handleExceed() {
      this.$message('只能选择一张图标！')
    },
    handleRowClick(type, row) {
      switch (type) {
        case 'add':
          // this.$refs.addForm.resetFields()
          this.dialogVisible = true
          this.operationType = 'add'
          this.addForm = {
            code: '',
            icon: '',
            name: '',
            parentId: 0,
            platformId: 0,
            route: '',
            sort: null,
            type: '1'
          }
          break
        case 'editor':
          this.operationType = 'editor'
          this.getMenuDetail(row.id)
          break
        case 'delete':
          this.getDelete(row)
          break
        case 'hidden':
          this.getHide(row)
          break
        case 'detail':
          this.operationType = 'detail'
          this.getMenuDetail(row.id)
          break
      }
    },
    // 详情
    getMenuDetail(id) {
      API.getMenuDetail({ id: id }).then(res => {
        if (res.status == 200) {
          this.addForm = res.data
          this.addForm.type = res.data.type.toString()
          res.data.parentIds.forEach(item => {
            item = item + ''
            console.log(item)
          })
          this.addForm.parentId = res.data.parentIds
          if (this.addForm.parentId.length == 0) {
            this.addForm.parentId = [-1]
          } else {
            this.addForm.parentId.splice(0, 0, -1)
          }
          // this.addForm.parentId = [-1, '3', '111']
          if (this.addForm.type == '1' && this.addForm.icon) {
            this.fileList.push({ url: this.addForm.icon })
          }
          // this.addForm.parentId = [...parseInt(res.data.parentId)]
          this.dialogVisible = true
        } else {
          this.$message.error(res.message)
        }
      })
    },
    // 删除
    getDelete(id) {
      const ids = []
      ids.push(id)
      this.$confirm('删除以后数据不可恢复，请谨慎操作！', '删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true
        API.getMenuDelete({ ids: ids })
          .then(res => {
            if (res.status == 200) {
              this.$message.success(res.message)
              this.getTableData()
            } else {
              this.$message.error(res.message)
            }
          })
          .finally(() => {
            this.loading = false
          })
      })
    },
    // 显示隐藏
    getHide(row) {
      var obj = {
        id: row.id,
        hided: !row.hided,
        name: row.name,
        type: row.type
      }
      var valueT = ''
      valueT = row.hided ? '隐藏' : '显示'
      this.$confirm(`是否${valueT}该菜单！`, `${valueT}`, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading = true
        API.getMenuModify(obj)
          .then(res => {
            if (res.status == 200) {
              this.$message.success(res.message)
              this.getTableData()
              this.getMenuTree()
            } else {
              this.$message.error(res.message)
            }
          })
          .finally(() => {
            this.loading = false
          })
      })
    },
    getTableData() {
      this.loading = true
      const form = {}
      form.platformId = parseInt(this.row.id)
      API.getMenuListAsc(form)
        .then(res => {
          if (res.status == 200) {
            this.pageInfo.total = res.data.total
            this.tableData = res.data
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
          // if (this.$route.query.pageNum) {
          //   this.$router.replace('/dataManage/datasetManage/list')
          // }
        })
    },
    handlePageChanged(value) {
      this.searchForm.pageNum = value
      this.getTableData(false)
    },
    handlePageSizeChanged(value) {
      this.searchForm.pageSize = value
      this.getTableData(false)
    }
  }
}
</script>

<style lang="scss" scoped></style>
